/* @import url('https://fonts.googleapis.com/css?family=Inconsolata|Lora'); */
@import url('https://fonts.googleapis.com/css?family=VT323|Ubuntu|Julee|Lora|Kalam|Merienda');
.App{
  text-align: center;
  background-color: #28282b;
  color: whitesmoke;
  height: 100vh;
  width: 100vw;
  font-family: Ubuntu;
  display: grid;
  grid-template-rows: 3.2cm auto;
}
h1{
  margin: 0px 0px;
  /* padding-top: 5vh; */
}
.switchM{
  cursor: pointer;
  background-color: blanchedalmond;
  border: none;
  font-weight: bold;
}
/* .nav{
  height: 35vh;
} */
.Lower{
  overflow: scroll;
}
.link{
  color: whitesmoke;
}
a{
  text-decoration: none;
}
a:hover{
  text-decoration: underline;
}
.nav{
  /* width: 20vw; */
  padding-left: 10vw;
  padding-right: 10vw;
  /* border: 1px white solid; */
}
.repo{
  color: blueviolet;
}
.menu{
  display: flex;
  flex-wrap: wrap;
  column-gap: .5cm;
}
.logoText{
  text-align: left;
  padding-top: .8cm;
  /* font-size: 10px; */
}
.mode{
  padding-top: .9cm;
  width: .8cm;
  height: .8cm;
  cursor: pointer;
}
.logoSection{
  display: flex;
  justify-content: space-between;
  
}
mark{
  /* color: blueviolet; */
  background-color: #E4EFD1;
  /* font-weight: bold; */
}

.aboutHeadText{
  color: blueviolet;
  font-size: 50px;
  text-decoration: underline;


}
#about{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 2vh;
  text-align: left;
}
.aboutText{
  /* font-size: Lora; */
  /* font-family: Lora; */
  font-family: Ubuntu;
  font-size: 20px;
}
.aboutTextPara{
  margin: 15px 0px;
}
.aboutImg{
  width: 7cm;
  height: 6cm;
  border: none;
  margin-top: 20px;
  border-radius: 10px;

}

#skills{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 5vh;
  text-align: left;
}
#contact{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 3vh;
  text-align: left;
}
.contactImgs{
  padding-left: 5vw;
  padding-right: 5vw;
  display: flex;
  column-gap: .3cm;
}
.contactImg{
  padding-top: 2vh;
  width: 1cm;
}
.contactForm{
	width: 400px;
	height: 350px;
	border: none;
	/* padding-top: 2vh; */
}
#projects{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 5vh;
  text-align: left;
  /* text-decoration: underline; */
}
.projectsMe1{
  padding-left: 5vw;
  padding-right: 5vw;
}
.projectsMe2{
  padding-left: 5vw;
  padding-right: 5vw;
}
.projectsMe1:hover{
  transform: scale(1.2x);
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
}
.projectsMe2:hover{
  transform: scale(1.2x);
  box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;
}
.projectName{
  color: blueviolet;
  font-size: 30px;
  font-family: Ubuntu;
  cursor: pointer;
  /* text-decoration: underline; */
}
.skillssss{
  padding-left: 5vw;
  padding-right: 5vw;
}

progress{
  padding-top: 1vh;
  padding-left: 3vw;
  color: blueviolet;
}

.skillName{
  color: blueviolet;
  font-size: 24px;
  font-family: Ubuntu;
}

.projectDetails{
  padding-top: 1vh;
  padding-left: 3vw;
}
.projectNameHead{
  display: flex;
  column-gap: .1cm;

  /* justify-content: center; */
  /* align-items: center; */
}
.direct{
  width: .5cm;
  height: .5cm;
  margin-top: 1.5vh;
  
  
}
.directKofi{
  width: .35cm;
  height: .35cm;
  margin-top: .2vh;
}
.footer{
    display: flex;
    justify-content: center;
    align-content: center;
    column-gap: .1cm;
    width: 100vw;
}

@keyframes typing {
  from { width: 0 }
  to { width: 100% }
}
.tagLine{
  margin: 0px 0px;
  font-size: 26px;
  
  color: blueviolet;
 
  font-family: Ubuntu;
}
.kofi{
  color: whitesmoke;
  padding-bottom: 2vh;
}
#kofiframe{
	height: 80vh;
	width: 380px;
	margin-top: 3vh;
  margin-left: 1vw;
	
	border: none;
	border-radius: 10px;
	box-shadow:rgba(0,0,0,0.5) 0px 5px 15px, inset rgba(0,0,0,0.1) 0px -10px 20px;


}
#support{
  padding-left: 10vw;
  padding-right: 10vw;
  padding-top: 5vh;
  text-align: left;
  /* text-decoration: underline; */
}
@media screen and (max-width:1000px){
	.abc{
		display: none;
	}
}
@media screen and (max-width:400px){
	#kofiframe{
		width: 300px;
	}
}

@media screen and (max-width:300px){
	#kofiframe{
		width: 250px;
	}
}


@media screen and (max-width:480px){
	.contactForm{
		width: 350px;
	}
  
	
}
@media screen and (max-width : 450px){
  .aboutHeadText{
    font-size: 45px;
  }
  /* .App{
    grid-template-rows: 4cm auto;
  } */
  .menu{
    line-height: .2cm;
  }
  .projectName{
    font-size: 26px;
  }
  .direct{
    width: .4cm;
    height: .4cm;
    margin-top: .3vh;
  }
  .aboutTextPara{
    font-size: 16px;
  }
  .tagLine{
    font-size: 22px;
  }
}

@media screen and (max-width:415px){
	.contactForm{
		width: 300px;	}
    .App{
      grid-template-rows: 4cm auto;
    }
    
}
@media screen and (max-width:400px){
  .aboutHeadText{
    font-size: 40px;
  }
}
@media screen and (max-width:385px){
	.contactForm{
		width: 250px;	}
    
	
}
@media screen and (max-width:330px){
	.contactForm{
		width: 200px;	}
    .aboutImg{
      width: 6cm;
      height: 5cm;
    }
    .App{
      grid-template-rows: 3.8cm auto;
    }
    h1{
      font-size: 30px;
    }
    .aboutHeadText{
      font-size: 37px;
    }
   
    .mode{
      width: .6cm;
      height: .6cm;
    }
    .menuItem{
      font-size: 13px;
    }
    .aboutText{
      font-size: 18px;
    }
    .projectName{
      font-size: 27px;
    }

    .contactImg{
      width: .8cm;
    }

}
@media screen and (max-width:290px){
  .projectName{
    font-size: 26px;
  }
  .direct{
    width: .4cm;
    height: .4cm;
    margin-top: .3vh;
  }
  .aboutImg{
    width: 5cm;
    height: 4cm;
  }
  .footer{
    width: 90vw;
  }
  .kofi{
    font-size: 15px;
  }
  .nav{
    padding-left: 3vw;
  }
  #about{
    padding-left: 3vw;
  }
  #skills{
    padding-left: 3vw;
  }
  #contact{
    padding-left: 3vw;
  }
  .contactImgs{
    padding-left: 2vw;
  }
  #projects{
    padding-left: 3vw;
  }
  .projectsMe1{
  padding-left: 2vw;
  /* padding-right: 5vw; */
  }
  .projectsMe2{
  padding-left: 2vw;
  /* padding-right: 5vw; */
  }
  .skillssss{
    padding-left: 2vw;
  }
  .aboutTextPara{
    font-size: 15.5px;
  }
  .tagLine{
    font-size:18px;
  }
}

@media screen and (min-width:1700px){
	
	.contactForm{
		width: 600px;
		height: 400px;
		
	}
	
}






@media screen and (max-width:230){
  .logoText{
    font-size: 8px;
  }
}


::-webkit-scrollbar {
  width: 10px;
  height: 0px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 

::-webkit-scrollbar-thumb {
  background: blueviolet; 
}


::-webkit-scrollbar-thumb:hover {
  background: blueviolet; 
}
